<template>
<div>
    <Row>
        <Col span="10">
        <h1>Ventas
            <Icon type="ios-box" /></h1>
        </Col>
        <Col span="2" offset="9">
        <Button @click="redirect('/sales/add')" type="info"><h3>Agregar Venta <Icon type="bag"></Icon></h3></Button>
        </Col>
    </Row>
    <br>
    <Table border :loading="loading" :columns="columns" :data="data"></Table>
    <br>
    <Col span="10" offset="8">
    <Page :total="100" prev-text="Previous" next-text="Next" />
    </Col>
</div>
</template>

<script>

export default {
    data() {
        return {
            data: [],
            loading: false,
            columns: [{
                    title: 'Cliente',
                    key: 'saleId',
                    render: (h, params) => {
                        return h('div', [
                            // h('Icon', {
                            //     props: {
                            //         type: 'person'
                            //     }
                            // }),
                            h('normal', params.row.client.name)
                        ]);
                    }
                },
                {
                    title: 'Empleado',
                    key: 'name',
                    render: (h, params) => {
                        return h('div', [
                            // h('Icon', {
                            //     props: {
                            //         type: 'person'
                            //     }
                            // }),
                            h('normal', params.row.user.name)
                        ]);
                    }
                },
                {
                    title: 'Fecha Factura',
                    key: 'registerDate',
                    render: (h, params) => {
                        let registerD = new Date(params.row.registerDate)
                        return h('div', [
                            ,
                            h('strong', `${registerD.getDate()}/${registerD.getMonth() + 1}/${registerD.getFullYear()}`)
                        ]);
                    }
                },
                {
                    title: 'Action',
                    key: 'action',
                    width: 150,
                    align: 'center',
                    render: (h, params) => {
                        return h('div', [
                            h('Button', {
                                props: {
                                    type: 'primary',
                                    size: 'small',
                                    icon: 'eye'
                                },
                                style: {
                                    marginRight: '5px'
                                },
                                on: {
                                    click: () => {
                                        this.show(params.index)
                                    }
                                }
                            }),
                            h('Button', {
                                props: {
                                    type: 'error',
                                    size: 'small',
                                    icon: 'close'
                                },
                                on: {
                                    click: () => {
                                        this.remove(params.index)
                                    }
                                }
                            })
                        ]);
                    }
                }
            ],
        }
    },
    created() {
        let sale = this
        sale.getAll()
    },
    methods: {
        getAll(){
            let sale = this
            sale.$Loading.start()
            sale.loading = true
            sale.$store.state.services.SaleService
            .getAll().then(p => {
                sale.$Loading.finish()
                sale.loading = false
                sale.data = p.data
            }).catch(e => {
                sale.$Loading.error()
            })
        },
        delete(id){
            let sale = this
            sale.loading = true
            sale.$store.state.services.SaleService
            .remove(id)
            .then(s => {
                sale.loading = false
                sale.getAll()
            })
            .catch(r => {
                 this.$Notice.error({
                    title: 'Error',
                    desc: ''
                    });
            });
        },
        show(index) {
            this.$router.push(`/sales/${this.data[index].saleId}/detail`)
        },
        remove(index) {
            this.$Modal.confirm({
                title: 'Eliminar esta Venta',
                okText: 'Confirmar',
                onOk: () => {
                    this.delete(this.data[index].saleId),
                    
                    this.$Notice.success({
                    title: 'Venta Eliminada',
                    desc: ''
                    });
                },
                cancelText: 'Cancelar',
                onCancel: () => {
                    
                }
            })
        },
        redirect(path) {
            if (path === undefined) return
            this.$router.push(path)
        }
    }
}
</script>



